<script setup>
import { ref, computed } from 'vue'

import PopUp from './PopUp.vue'
import Button from './Button.vue'
import FormTwoHead from './FormTwoHead.vue'

const isShowPopUp = ref(false)
const selectedStationery = ref(null)
const isChecked = ref(false)
const isFail = ref(false)

const textArr = ref([
	{
		text: '1',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'рядовой',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'солдат',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: '654321',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'А',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'ОВК Бирюлевского района г. Москвы',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'общий',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'Военный билет АА №1234567, выдан ОВК Бирюлевского района г. Москвы 01.04.2000',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'Прием на работу',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'Менеджер',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'ОВК Бирюлевского района г. Москвы ',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'Дата и исх. № уведомления когда отправили сведения в военкомат',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'Специалист по воинскому учету',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'Цветкова',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'А.А. Цветкова',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'Иванов',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'Г.С. Иванов',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: '11',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: 'января',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
	{
		text: '24',
		isPen: true,
		isFilled: false,
		isFail: null,
		htmlElement: null,
	},
])

const handleTextClick = index => {
	//	if (isChecked.value) return
	if (selectedStationery.value === null) return
	const textElement = document.querySelector(`.p${index}`)
	if (textElement) {
		textArr.value[index].htmlElement = textElement
		textElement.classList.remove('text-animation')
		textElement.classList.remove('pen')
		textElement.classList.remove('pencil')
		textElement.classList.remove('crossed-out')
		textElement.classList.add(selectedStationery.value)
		textElement.textContent = textArr.value[index].text
		textArr.value[index].isFilled = true
		if (textArr.value[index].isPen && selectedStationery.value === 'pen') {
			textArr.value[index].isFail = false
		} else if (
			!textArr.value[index].isPen &&
			selectedStationery.value !== 'pen'
		) {
			textArr.value[index].isFail = false
		} else {
			textArr.value[index].isFail = true
		}
	}
}

const selectStationery = stationery => {
	selectedStationery.value = stationery
}

const checkUserAnswer = () => {
	isShowPopUp.value = true
	isChecked.value = true
	let failArr = textArr.value.filter(item => item.isFail === true)
	failArr.forEach(item => {
		item.htmlElement.classList.add('crossed-out')
		//item.htmlElement.classList.add('error')
	})
	if (failArr.length > 0) isFail.value = true
	else isFail.value = false
}

const isEnd = computed(() => {
	return textArr.value.every(item => item.isFilled === true)
})
</script>

<template>
	<PopUp
		v-if="isShowPopUp"
		:isFail="isFail"
		:isLastPage="true"
		@hide="isShowPopUp = false"
		@nextPage="$emit('nextPage')"
	></PopUp>
	<div class="container">
		<div class="container__img">
			<img
				src="../assets/Ручка-Photoroom.png"
