					</p>
				</div>

				<div class="">3. Состав (профиль)</div>
				<div class="border center-elem">
					<p @click="handleTextClick(2)" class="pointer text-animation p2">
						Заполните
					</p>
				</div>

				<div class="">4. Полное кодовое обозначение ВУС</div>
				<div class="border center-elem max-h">
					<p @click="handleTextClick(3)" class="pointer text-animation p3">
						Заполните
					</p>
				</div>

				<div class="">5. Категория годности к военной службе</div>
				<div class="border center-elem max-h">
					<p @click="handleTextClick(4)" class="pointer text-animation p4">
						Заполните
					</p>
				</div>
			</div>

			<div class="container__grid-2-1 right-column">
				<div class="">
					6. Наименование военного комиссариата по месту воинского учета
				</div>
				<div class="border center-elem">
					<p @click="handleTextClick(5)" class="pointer text-animation p5">
						Заполните
					</p>
				</div>

				<div class="full-col">7. Состоит на воинском учете:</div>
				<div>1) общем</div>
				<div class="border center-elem">
					<p @click="handleTextClick(6)" class="pointer text-animation p6">
						Заполните
					</p>
				</div>
				<div></div>
				<div class="footnote_text mt_-1">(номер команды, партии)</div>
				<div>2) специальном</div>
				<div class="border"></div>
				<div></div>
				<div class="footnote_text mt_-1 text-center">
					(номер удостоверения об отсрочке или именного списка о зачислении на
					спецучет)
				</div>
				<div>8. Документ воинского учета</div>
				<div class="border center-elem min-h">
					<p @click="handleTextClick(7)" class="pointer text-animation p7">
						Заполните
					</p>
				</div>
				<div></div>
				<div class="footnote_text mt_-1 text-center">
					(наименование, серия и номер документа, кем и когда выдан)
				</div>
			</div>
		</div>

		<div class="container_head">
			<h3>III. Дополнительные сведения</h3>
			<div class="border-bottom"></div>
			<div class="border-bottom"></div>
			<div class="border-bottom"></div>
			<div class="border-bottom"></div>
			<div class="border-bottom"></div>
			<div class="border-bottom"></div>
		</div>
		<div class="container_head">
			<h3>IV. Сведения о приеме и увольнении (переводе)</h3>
		</div>

		<div class="container__table">
			<table>
				<tr>
					<th>Дата и номер приказа (распоряжения)</th>
					<th style="width: 40vw">
