					<th>В какой военный комиссариат направлены сведения</th>
					<th>Дата и исходящий номер сведений гражданина</th>
				</tr>
				<tr>
					<td>
						<p @click="handleTextClick(8)" class="pointer text-animation p8">
							Заполните
						</p>
					</td>
					<td>
						<p @click="handleTextClick(9)" class="pointer text-animation p9">
							Заполните
						</p>
					</td>
					<td>
						<p @click="handleTextClick(10)" class="pointer text-animation p10">
							Заполните
						</p>
					</td>
					<td>
						<p @click="handleTextClick(11)" class="pointer text-animation p11">
							Заполните
						</p>
					</td>
				</tr>
				<tr>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
				<tr>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
				<tr>
					<td></td>
