				<div class="element_bottom border-bottom">
					<p @click="handleTextClick(13)" class="pointer text-animation p13">
						Заполните
					</p>
				</div>
				<div class="element_bottom border-bottom">
					<p @click="handleTextClick(14)" class="pointer text-animation p14">
						Заполните
					</p>
				</div>
				<div></div>
				<div class="footnote_text">(должность)</div>
				<div class="footnote_text">(подпись)</div>
