<style scoped>
.container {
	margin: 0 0.3vw;
}
.container__img {
	margin-top: 2vw;
}

.pen_pos {
	position: absolute;
	left: 2vw;
	z-index: 50;
}

.pencil_pos {
	position: absolute;
	left: 15vw;
	z-index: 50;
}

.pen {
	color: #4169e1;
	font-family: 'BadScript', 'ProximaNova', sans-serif;
	font-weight: 900;
	padding: 0 1vw;
	text-align: center;
}

.pencil {
	color: #494d4e;
	font-family: 'BadScript', 'ProximaNova', sans-serif;
	font-weight: 900;
	padding: 0 1vw;
	text-align: center;
}

.container__img img {
	max-width: 10vw;
	border: 1px solid black;
	border-radius: 50%;
	padding: 0.5vw;
	cursor: pointer;
	transition: scale 0.7s, background-color 1s;
}

.container__img img:hover {
	scale: 1.05;
}

.container__img img.selected {
	background-color: rgba(0, 0, 0, 0.392);
	scale: 1.1; /* Увеличение выбранного элемента */
}

.container__grid-2 {
	margin-top: 2vw;
	display: grid;
	grid-template-columns: 1fr 1fr;
	position: relative;
	gap: 2vw;
	margin-bottom: 2vw;
}

.container__grid-2::before {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 50%; /* Линия будет по центру между колонками */
	width: 1px; /* Толщина линии */
	background-color: black; /* Цвет линии */
}

.container__grid-2-1 {
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.left-column {
	display: grid;
	grid-template-rows: repeat(5, auto);
	gap: 0.5vw; /* Уменьшенное расстояние между элементами левой колонки */
}

.right-column {
	display: grid;
	grid-auto-rows: auto;
	gap: 2vw; /* Стандартное расстояние между элементами правой колонки */
}

.border {
	border: 1px solid black;
}

.text-animation {
	animation: pulse 0.7s infinite alternate;
	cursor: pointer;
	color: red;
}

.pointer {
	cursor: pointer;
	transition: color 0.7s;
}

.center-elem {
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
	align-items: center;
}

.center-elem-y {
	display: flex;
	flex-flow: column nowrap;
	justify-content: center;
}

.min-h {
	min-height: 10vw;
}

.max-h {
	max-height: 7vw;
}

@keyframes pulse {
	from {
		scale: 1;
	}
	to {
		scale: 1.03;
	}
}

.max-h {
	max-height: 7vw;
}

.footnote_text {
	font-size: 0.45rem;
	display: flex;
	justify-content: center;
	margin-bottom: 1vw;
}

.mt_-1 {
	margin-top: -2vw;
}

.text-center {
	text-align: center;
}

.container_head h3 {
	margin-top: 2vw;
	text-align: center;
	font-size: 1.2rem;
}

.border-bottom {
	border-bottom: 1px solid black;
	min-height: 3vw;
}

.container__table table {
	width: calc(100% - 2vw);
	margin: 1vw 1vw;
}

table {
	width: 100%;
	border-collapse: collapse;
}

th,
td {
	border: 1px solid black; /* Цвет границы */
	padding: 8px; /* Отступ внутри ячеек */
}

th {
	font-weight: 500;
}

.container__signature {
	margin-top: 3.5vw;
}

.container__signature-grid-4 {
	display: grid;
	grid-template-columns: 1fr 1fr 0.7fr 0.7fr;
	gap: 0 2vw;
}

.element_bottom {
	display: flex;
	justify-content: center;
	align-items: end;
}

.container__date {
	max-width: 45%;
	display: grid;
	grid-template-columns: 0.6fr 1fr 0.6fr;
	gap: 0 1vw;
}

.container__mark-grid {
	margin-top: 1.5vw;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 0 2vw;
}

.full-col {
	grid-column: 1/-1;
}

.error {
	text-decoration: line-through;
	text-decoration-color: red;
	text-decoration-thickness: 3px;
}

.crossed-out {
	position: relative;
	display: inline-block;
	overflow: hidden; /* Скрываем линии, выходящие за границы */
}

.crossed-out::before,
.crossed-out::after {
	content: '';
	position: absolute;
	width: 141%; /* 100% * sqrt(2) для диагонали квадрата */
	height: 2px; /* Толщина линий */
	background-color: red; /* Цвет линий */
	top: 50%;
	left: -20.5%; /* Смещение для центрирования */
}

.crossed-out::before {
	transform: rotate(10deg); /* Первая линия под углом 45 градусов */
}

.crossed-out::after {
	transform: rotate(-10deg); /* Вторая линия под углом -45 градусов */
}

.container__btn {
	margin-top: 2vw;
	display: flex;
	justify-content: center;
	align-items: center;
}
</style>
